.mainBlock {
  display: flex;
  flex-direction: column;
  justify-content: "center";
  align-items: center;
  margin: 180px auto 0;
  gap: 24px;
  width: 480px;
}

.title {
  width: 100%;
  text-align: center;
}

.bottomBlock {
  display: flex;
  align-items: center;
  margin-top: 56px;
}

.bottomBlockActions {
  display: flex;
}

.bottomLink {
  color: #4c4cff;
  padding: 0;
  text-decoration: none;
  transition: 0.3s;
}

.bottomLink:hover {
  color: white;
}
