.mainBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 520px;
  height: 498px;
  padding: 20px 100px 120px 100px;
}

.orderId {
  display: flex;
  justify-content: center;
  text-align: center;
  text-shadow: 0px 4px 32px rgba(51, 51, 255, 0.5),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 0px 16px rgba(51, 51, 255, 0.25);
  margin: 0;
}

.orderIdText {
  text-align: center;
}

.done {
  width: 120px;
  height: 120px;
}

.orderStatus {
  margin-top: 0;
}

.orderText {
  text-align: center;
  margin: 0;
}
